import React, {useEffect} from 'react';
import * as PIXI from 'pixi.js';

const drawLines = (triangles, x1, y1, x2, y2, x3, y3) => {
    triangles.moveTo(x1, y1);
    triangles.lineTo(x2, y2);
    triangles.lineTo(x3, y3);
    triangles.closePath();
}

const updateLines = (triangles, xMouse, yMouse, x1, y1, x2, y2, x3, y3, alt) => {
    triangles.moveTo(x1 + Math.cos(xMouse) * 20, y1 + Math.sin(yMouse) * 20);
    triangles.lineTo(x2 + Math.cos(xMouse) * 20, y2 + Math.sin(yMouse) * 20);
    triangles.lineTo(x3 + (alt ? Math.cos(xMouse) : Math.sin(xMouse)) * 20, y3 + (alt ? Math.sin(yMouse) : Math.cos(yMouse)) * 20);
    triangles.closePath();
}

const points = [[50, 50, 250, 50, 100, 100], [250, 50, 200, 20, 270, -100], [250, 50, 300, 60, 270, -100], [270, 90, 130, 150, 100, 100], [130, 150, 60, 140, 20, 200], [30, 250, 130, 150, 20, 200], [60, 140, 60, 100, 100, 100], [30, 80, 60, 140, 20, 200], [10, 250, 30, 250, 20, 200], [10, 550, -10, 400, 100, 450], [-10, 400, 30, 300, 100, 450], [100, 250, 30, 300, 100, 450], [130, 150, 175, 400, 100, 450], [270, 90, 130, 150, 250, 200], [175, 400, 130, 150, 250, 200], [175, 400, 275, 350, 250, 200], [240, 460, 175, 400, 100, 450], [240, 460, 200, 550, 100, 450], [10, 550, 200, 550, 100, 450], [100, 250, 130, 150, 100, 450],
//Non-cosine
[250, 50, 200, 20, 50, 50], [250, 50, 300, 60, 270, 90], [60, 100, 30, 80, 50, 50], [10, 250, -30, 270, 100, 250], [-30, 270, -10, 400, 30, 300], [-10, 400, -150, 600, 10, 550]]

const Pixi = () => {
    useEffect(() => {
        const container = document.getElementById("pixi-container");
        console.log(container.getBoundingClientRect().width)
        const app = new PIXI.Application({autoResize: true, width: container.getBoundingClientRect().width, height: container.getBoundingClientRect().height, backgroundAlpha: 0, antialias: true});
        container.appendChild(app.view);
        app.stage.interactive = true;
        const triangles = new PIXI.Graphics();
        triangles.lineStyle(2, 0xB9FFAD, 1);
        points.map(point => drawLines(triangles, ...point))
        triangles.x = app.screen.width - 300;
        triangles.y = 0;
        app.stage.addChild(triangles);

        window.addEventListener('resize', resize);
        function resize() {
            const clientRect = container.getBoundingClientRect()
            app.renderer.resize(clientRect.width, clientRect.height);
            triangles.x = app.screen.width - 300;
            //const widthMultiplier = clientRect.width / 1200
            //triangles.scale = (1, 1) * widthMultiplier
        }

        container.addEventListener('mousemove', (e) => {
            triangles.clear();
            triangles.lineStyle(2, 0xB9FFAD, 1);

            var yPercentage = 5 * (e.clientY / window.innerHeight);
            var xPercentage = 5 * (e.clientX / window.innerWidth);
            for(var i = 0; i < points.length; i++){
                updateLines(triangles, xPercentage, yPercentage, ...points[i], i > 19)
            }
        })
    }, [])

    return(
        <div id="pixi-container" />
    )
}

export default Pixi;